.banner_section{
      position: absolute;
      width: calc(100% - 4rem);
      height: calc(100% - 4rem);
      top: 2rem;
      left: 2rem;
      border-radius: 2rem;
      overflow: hidden;
}

.banner_section:after{
      width: 100%;
      height: 100%;
      background: linear-gradient(115deg, transparent 35%, var(--bg-100) 35.1%);
      top: 0;
      left: 0;
      z-index: 3;
}

.main_banner{
      width: 100%;
      height: 100%;
      border: solid .2rem var(--bg-100);
      border-radius: 2rem;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      background: linear-gradient(135deg, var(--dark-100), var(--dark-200));
}

.main_banner img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
      mix-blend-mode: luminosity;
}