.pager_inner__box.video{
      display: flex;
      align-items: center;
      justify-content: center;
}

.wrapper_video{
      width: 44.2rem;
      height: auto;
      position: relative;
      z-index: 10;
}

.video_box{
      width: 44.2rem;
      height: 25.5rem;
      border-bottom: solid .5rem var(--theme-100);
      background: #222;
      position: relative;
}

.video_box:before{
      width: 60%;
      height: 5rem;
      background: #000;
      border-radius: 50%;
      left: 20%;
      bottom: -1rem;
      filter: blur(2rem);
      z-index: -1;
      opacity: .5;
}

.video_box iframe{
      border: none;
}