.pager_inner__box.friend{
      display: flex;
      align-items: center;
      justify-content: center;
}

.friend_content{
      width: 52rem;
      height: 22rem;
}

.friend_card{
      width: 16rem;
      height: 22rem;
      background: var(--theme-100);
      border-radius: 1rem;
      padding: 1rem;
      position: relative;
      overflow: hidden;
}

.friend_card a{
      width: 14rem;
      height: 20rem;
      border: solid .1rem var(--theme-100);
      border-radius: 1rem;
      overflow: hidden;
      position: relative;
      background: linear-gradient(135deg, var(--theme-100), var(--theme-200));
}

.friend_card a:before{
      width: 200%;
      height: 100%;
      background: var(--theme-200);
      left: 0;
      top: 65%;
      transform: translate(-30%, 0) rotate(-20deg);
      z-index: 2;
}

.friend_card a img{
      width: 14rem;
      height: 20rem;
      position: absolute;
      top: 0;
      left: 0;
      mix-blend-mode: luminosity;
      border-radius: 1rem;
      border: solid .1rem var(--theme-200);
      transition: .2s;
}

.friend_card a:hover img{
      mix-blend-mode: normal;
}

.friend_card a strong{
      position: absolute;
      width: 100%;
      text-align: center;
      color: var(--bg-100);
      z-index: 3;
      top: 105%;
      left: 0;
      font-size: 2rem;
      transition: .2s ease-in-out;
      font-weight: 500;
}

.friend_card:hover a strong{
      top: 80%;
}

.short_name{
      position: absolute;
      width: 4rem;
      height: 4rem;
      background: var(--bg-100);
      z-index: 3;
      left: calc(50% - 2rem);
      top: 50%;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      color: var(--theme-100);
      text-transform: uppercase;
      font-size: 2rem;
      font-weight: 600;
      box-shadow: 0 0 2rem rgba(0,0,0,.1);
}

.slick-list{
      height: 22rem;
}

.slick-slide{
      margin: 0 .74rem;
}

.slick-arrow{
      display: none;
      visibility: hidden;
}