@import url('https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@100;200;300;400;500;600;700;800&family=Chivo+Mono:wght@100;200;300;400;500;600&family=Genos:wght@100;200;300;400;500;600&display=swap');
:root{
  /** fonts */
  --ft-1: 'Genos', sans-serif;
  --ft-2: 'Big Shoulders Display', cursive;
  --ft-3: 'Chico Mono', monospace;

  /** colors */
  --green: #45d985;
  --light: #fff;
  --red-200: #d11e22;
  --red-100: #e90d0f;
  --alpha: rgba(0,0,0,.05 );
  --grys-300: #222;
  --grys-200: #c9d8db;
  --grys-100: #fcffff;

  /** default theme */
  --theme-200: #d11e22;
  --theme-100: #c31711;
  --bg-200: #edefdd;
  --bg-100: #f6f8e7;
  --dark-200: #202b3d;
  --dark-100: #35455c;

  /** theme 3 */
  --theme-200: #6237a0;
  --theme-100: #9754cb;

  /** theme 4 */
  --theme-200: #036ed9;
  --theme-100: #2196F3;

  /** theme 2 */
  --theme-200: #c45c81;
  --theme-100: #c8798e;

  /** social color*/
  --facebook: #3b5995;
  --twitter: #1da1f2;
  --google: #dd4b39;
  --youtube: #c4302b;
  --vimeo: #1ab7ea;
  --dribbble: #ea4c89;
  --github: #333;
  --instagram: #c13584;
  --pinterest: #e60023;
  --flickr: #0063dc;
  --bitbucket: #205081;
  --linkedin: #0e76a8;
  --whatsapp: #12af0a;
  --tumblr: #35465c;
  --tiktok: #222;
  --discord: #5865f2;
  
  /** globals */
  font-family: var(--ft-1);
  font-size: 62.5%;
  color: var(--light);

}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  min-height: 98rem;
  overflow-x: hidden;  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--dark-200);
}

body *{
  font-size: 1rem;
}

:before,
:after{
  content: '';
  position: absolute;
}

a, span{
  display: inline-block;
}

a{
  text-decoration: none;
}

ul{
  list-style: none;
}

img{
  width: 100%;
  object-fit: cover;
}

button{
  border: none;
  outline: none;
  cursor: pointer;
  font-family: var(--ft-2);
}

.bg_grad{
  display: inline-block;
  color: transparent;
  background: var(--gradient-45);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
}

::-webkit-scrollbar{
  width: .8rem;
  background: var(--alpha);
  border-radius: .8rem
}

::-webkit-scrollbar-thumb{
  background: var(--theme-100);
  border-radius: .8rem;
}