.btn_social{
      width: 4.2rem;
      height: 4.2rem;
      background: var(--bg-200);
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: .2s;
}

.btn_social *{
      font-size: 1.8rem;
      color: var(--theme-100);
      transition: .2s;
}

.btn_social:hover{
      background: var(--theme-100);
}

.btn_social:hover *{
      color: var(--bg-100);
}