.pager_inner__box.relation{
      padding: 3rem;
      display: flex;
      gap: 2rem;
}

.wrapper_relation{
      width: 44rem;
      height: 100%;
      display: flex;
      gap: 2rem;
      flex-shrink: 0;
}

.relation_data{
      display: flex;
      flex-direction: column;
      align-items: center;
}

.relation_data h2{
      color: var(--theme-100);
      font-size: 3.5rem;
}

.relation_data span{
      color: var(--grys-300);
      font-size: 2.5rem;
}

.relation_data strong{
      font-size: 4rem;
      color: var(--grys-300);
      line-height: 3rem;
}

.relation_data a{
      text-transform: uppercase;
      background: var(--theme-100);
      color: var(--bg-100);
      font-size: 2rem;
      padding: 0 2rem;
      border-radius: 999px;
      box-shadow: 1rem 1rem 2rem rgba(0,0,0,.2);
      margin-top: 2rem;
      height: 3.2rem;
      line-height: 3rem;
}

.relation_about{
      height: 25rem;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--alpha) var(--theme-100);
}

.relation_about p{
      font-size: 1.8rem;
      color: var(--grys-300);
}

.relation_avatar{
      width: 22rem;
      height: 100%;
      position: relative;
}

.relation_avatar__img{
      width: 22rem;
      height: 22rem;
      border: solid 2rem var(--bg-100);
      background: linear-gradient(135deg, var(--dark-100), var(--dark-200));
      border-radius: 50%;
      position: absolute;
      left: 0;
      top: calc(50% - 11rem);
      box-shadow: 0 0 3rem rgba(0,0,0,.1);
}

.relation_avatar__img img{
      width: 18rem;
      height: 18rem;
      border-radius: 50%;
      mix-blend-mode: screen;
}