.banner_content{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 50vh;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      background: linear-gradient(var(--dark-100), var(--dark-200));
}

.banner_content:after{
      width: 100%;
      height: 100%;
      background: linear-gradient(transparent, var(--dark-200) 75%);
      top: 0;
      left: 0;
}

.banner_content img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
      mix-blend-mode: screen;
}