.empty_content{
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      gap: 2rem;
      align-items: center;
      justify-content: center;
}

.empty_icon *{
      color: var(--bg-200);
      font-size: 10rem;
}

.empty_content h3{
      font-size: 2.5rem;
      color: rgba(0,0,0,.2);
}