.container{
      position: relative;
      width: 98rem;
      height: auto;
      min-height: 100vh;
      margin: 0 auto;
      display: flex;
      flex-direction: column;
}

.wrapper_content{
      position: relative;
      width: 100%;
      height: 38rem;
      margin-top: 25rem;
      background: var(--bg-100);
      padding: 2rem;
      border-radius: 2rem;
      box-shadow: -1rem -1rem 2rem rgba(0,0,0,.2), 2rem 2rem #111;
}

.main_content{
      position: relative;
      width: 100%;
      height: 100%;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      z-index: 10;
      gap: 2rem;
}

.wrapper_menu{
      position: relative;
      padding: 1rem;
      height: 100%;
}

.btn_toggle{
      width: 3.6rem;
      height: 3.6rem;
      position: relative;
      background: none;
}

.btn_toggle span{
      width: 3.6rem;
      height: .3rem;
      background: var(--bg-100);
      position: absolute;
      top: calc(50% - 0.15rem);
      left: calc(50% - 1.8rem);
      border-radius: .3rem;
      transition: .2s;
}

.btn_toggle span:nth-child(1){
      transform: translateY(-1rem);
}

.btn_toggle span:nth-child(3){
      transform: translateY(1rem);
}

.btn_toggle.active span:nth-child(1),
.btn_toggle.active span:nth-child(2){
      transform: translateX(0) rotate(45deg);
}

.btn_toggle.active span:nth-child(3){
      transform: translateY(0) rotate(-45deg);
}

.toggle_menu{
      width: 18rem;
      height: 21.4rem;
      background: var(--bg-100);
      border-radius: 1rem;
      padding: 1rem;
      box-shadow: 1rem 1rem #111;
      position: absolute;
      top: 5rem;
      left: 5rem;
      opacity: 0;
      visibility: hidden;
      transform: scale(0);
      transform-origin: left top;
}

.toggle_menu.active{
      animation: show_menu .3s ease-in-out forwards;
}

@keyframes show_menu{
      0%{
            opacity: 0;
            visibility: hidden;
            transform: scale(0)
      }
      80%{
            opacity: 1;
            visibility: visible;
            transform: scale(1.4)
      }
      100%{
            opacity: 1;
            visibility: visible;
            transform: scale(1)
      }
}

.toggle_menu ul{
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
}

.toggle_menu ul li{
      height: 3.8rem;
      width: 100%;
      display: flex;
      align-items: center;
      padding-left: 1rem;
      gap: 1.5rem;
}

.toggle_menu ul li *{
      color: var(--grys-300);
      font-size: 2.2rem;
      transition: .2s;
}

.toggle_menu ul li a,
.toggle_menu ul li span{
      height: 3.8rem;
      width: 10rem;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 3.8rem;
      background: var(--bg-200);
      font-size: 1.6rem;
      text-transform: uppercase;
      cursor: pointer;
}

.toggle_menu ul li:hover *,
.toggle_menu ul li.active *{
      color: var(--theme-100);
}

.toggle_menu ul li:hover a,
.toggle_menu ul li:hover span,
.toggle_menu ul li.active span{
      background: var(--theme-100);
      color: var(--bg-100);
}

.wrapper_user__avatar{
      width: 24rem;
      height: 24rem;
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 10;
      border-radius: 50%;
      padding: 3rem;
      background: linear-gradient(var(--theme-100) 50%, var(--bg-100) 50%);
      box-shadow: 0 0 2rem rgba(0,0,0,.1);
}

.wrapper_user__avatar:before{
      width: calc(100% - 2.8rem);
      height: calc(100% - 2.8rem);
      border-radius: 50%;
      box-shadow: 0 0 0 .3rem var(--bg-100);
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
}

.wrapper_user__avatar:after{
      width: 1.3rem;
      height: 1.3rem;
      background: var(--theme-100);
      box-shadow: 0 0 0 .3rem var(--bg-100);
      top: 5rem;
      left: 2.8rem;
      border-radius: 50%;
}

.user_avatar__img,
.user_avatar__img img{
      width: 18rem;
      height: 18rem;
      border-radius: 50%;
      display: flex;
}

.user_avatar__img{
      background: linear-gradient(135deg, var(--dark-100), var(--dark-200));
}

.user_avatar__img img{
      mix-blend-mode: screen;
}

.user_avatar__initials{
      width: 4rem;
      height: 4rem;
      background: var(--theme-100);
      display: flex;
      align-items: flex-start;
      justify-content: center;
      text-transform: uppercase;
      border-radius: 50%;
      left: calc(50% - 2rem);
      bottom: -2rem;
      position: absolute;
      font-size: 2.8rem;
      font-weight: 500;
      line-height: 3.5rem;
      color: var(--bg-100);
}

.wrapper_user{
      display: flex;
      flex-direction: column;
      align-items: center;
      color: var(--grys-300);
}

.wrapper_user h2{
      margin-top: 13rem;
      font-size: 3.4rem;
      color: var(--theme-100);
}

.wrapper_user p{
      font-size: 1.8rem;
      text-align: center;
}

.wrapper_user strong{
      font-size: 3.6rem;
      color: var(--theme-100);
}

.user_menu{
      margin-top: 2rem;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 1rem;
}

.user_menu a{
      width: 4.2rem;
      height: 4.2rem;
      background: var(--bg-200);
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: .2s;
}

.user_menu a *{
      font-size: 1.8rem;
      color: var(--theme-100);
      transition: .2s;
}

.user_menu a:hover *{
      color: var(--bg-100);
}

.user_menu a:hover{
      background: var(--theme-100);
}

.about_user{
      height: 100%;
      gap: 1rem;
}

.wrapper_online{
      width: fit-content;
      height: 5rem;
      display: flex;
      align-items: center;
      gap: 1rem;
      color: var(--grys-300);
      margin-top: 3rem;
}

.online_avatar{
      width: 5rem;
      height: 5rem;
      border-radius: 50%;
      padding: .5rem;
}

.online_avatar.green{
      box-shadow: 0 0 0 .3rem var(--green) inset;
}

.online_avatar.red{
      box-shadow: 0 0 0 .3rem var(--red-100) inset;
}

.online_avatar img{
      width: 4rem;
      height: 4rem;
      border-radius: 50%;
}

.wrapper_online span{
      font-size: 1.8rem;
}

.about_user__description{
      width: 25rem;
      height: auto;
      max-height: 21rem;
      overflow-x: hidden;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--alpha) var(--theme-100);
      margin-top: 2rem;
}

.about_user__description p{
      color: var(--grys-300);
      font-size: 1.8rem;
}

.main_title{
      width: 37rem;
      height: 6rem;
      border-radius: 999px;
      background: var(--bg-100);
      box-shadow: 1rem 1rem 2rem rgba(0,0,0,.1);
      position: absolute;
      top: -3rem;
      right: -1rem;
      padding: 1rem 1rem 1rem 5rem;
      z-index: 10;
}

.main_title span{
      width: 100%;
      height: 100%;
      background: var(--bg-200);
      border-radius: 999px;
      line-height: 4rem;
      padding-left: 1rem;
      color: var(--grys-300);
      font-size: 1.8rem;
}

.wrapper_social{
      width: 40rem;
      height: 16rem;
      padding: 2rem;
      background: var(--bg-100);
      border-radius: 2rem;
      box-shadow: -1.5rem -1.5rem 2rem rgba(0,0,0,.05), 1rem 1rem #111;
      position: absolute;
      top: calc(100% - 3rem);
      left: calc(50% - 20rem);
      z-index: 10;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 2rem;
}

.wrapper_social span{
      color: var(--grys-300);
      font-size: 2.5rem;
}

.social_menu{
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 1rem;
}

.wrapper_pager{
      width: 84rem;
      height: 37rem;
      padding: 2rem;
      background: var(--bg-100);
      border-radius: 2rem;
      position: absolute;
      top: calc(63rem - 5rem);
      right: -2rem;
      z-index: 20;
      box-shadow: -1.5rem -1.5rem 2rem rgba(0,0,0,.05), 2rem 2rem #111;
      opacity: 0;
      visibility: hidden;
      transform: scale(0);
      transform-origin: left top;
}

.wrapper_pager.show{
      animation: show_page .4s ease-in-out forwards;
}

@keyframes show_page{
      0%{
            opacity: 0;
            visibility: hidden;
            transform: scale(0);
      }
      80%{
            opacity: 1;
            visibility: visible;
            transform: scale(1.4);
      }
      100%{
            opacity: 1;
            visibility: visible;
            transform: scale(1);
      }
}

.btn_close{
      width: 4rem;
      height: 4rem;
      background: var(--bg-100);
      border-radius: 50%;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(50%, -50%);
      box-shadow: 0 0 2rem rgba(0,0,0,.1);
      display: flex;
      align-items: center;
      justify-content: center;
}

.btn_close *{
      font-size: 2rem;
}

.page_content{
      width: 100%;
      height: 100%;
      border-radius: 2rem;
}

.page_inner{
      width: 100%;
      height: 29.4rem;
      position: relative;
      margin-top: 1rem;
}

.pager_inner__box{
      width: 100%;
      height: 100%;
      position: relative;
      z-index: 10;
}